
.c-swipe{
  overflow: hidden;
}
.c-swipe-warpper{
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
}
.c-swipe-item{
  width: 100%;
  height: 100%;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
  position: absolute;
}
.c-swipe-item.active{
  -webkit-transform: translate3d(0 ,0 ,0);
          transform: translate3d(0 ,0 ,0);
}
/*.c-swipe-item.active ~ .c-swipe-item{
  transform: translate3d(100%, 0, 0);
}*/
.c-swipe-pagination{
  position: relative;
  height: 0;
}
.c-swipe-pagination-bar{
  position: absolute;
  left: 0;
  right: 0;
  top: -12px;
  bottom: 0;
  height: 4px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
}
.c-swipe-pagination-item{
  display: block;
  width: 8px;
  height: 4px;
  border-radius: 3px;
  background-color: rgb(181,181,181);
  margin: 0 3px;
  transition: all .1s;
}
.c-swipe-pagination-item.active{
  width: 20px;
  background-color: #f66b5b;
}



